module.exports = "<div class=\"list-product-container\" *ngIf=\"!isHideList\">\r\n    <div class=\"col-md-3\">\r\n        <div class=\"no-margin col-md-12 choose-category\">\r\n            Chn danh mc\r\n        </div>\r\n        <div class=\"no-margin col-md-12\">\r\n            <danh-muc-selection [danhmucs]=\"danhmucs\" (onSelection)=\"selectDanhMuc($event)\"></danh-muc-selection>\r\n        </div>\r\n    </div>\r\n\r\n    <div class=\"col-md-9\">\r\n        <div class=\"no-margin row\">\r\n            <button class=\"btn-add-product pull-right\" (click)=\"addProduct()\" *ngIf=\"allowCreate\">\r\n                To mi sn phm\r\n            </button>\r\n        </div>\r\n        \r\n        <div class=\"no-margin row product-header\">\r\n            <div class=\"no-margin col-md-2 product-code\">M sn phm</div>\r\n            <div class=\"no-margin col-md-3\">Ten SP</div>\r\n            <div class=\"no-margin col-md-1\">Gia goc</div>\r\n            <div class=\"no-margin col-md-1\">Gia ban</div>\r\n            <div class=\"no-margin col-md-1\">Thu tu</div>\r\n            <div class=\"no-margin col-md-2\">Trang thai</div>\r\n            <div class=\"no-margin col-md-2\">Don vi</div>\r\n        </div>\r\n\r\n        <div class=\"product-table-content\">\r\n            <div class=\"no-margin row product-item\" *ngFor=\"let product of products\">\r\n                <div class=\"no-margin col-md-2 product-code\">{{product.code}}</div>\r\n                <div class=\"no-margin col-md-3\">{{product.name}}</div>\r\n                <div class=\"no-margin col-md-1\">{{product.priceOriginal}}</div>\r\n                <div class=\"no-margin col-md-1\">{{product.priceSell}}</div>\r\n                <div class=\"no-margin col-md-1\">{{product.position}}</div>\r\n                <div class=\"no-margin col-md-2\">{{product.status}}</div>\r\n                <div class=\"no-margin col-md-2\">\r\n                    {{product.unit}}\r\n                    <div class=\"pull-right btn-group-product\">\r\n                        <i class=\"fa fa-eye fa-lg\" title=\"Chi Tiet\" (click)=\"viewProduct(product)\"></i>\r\n                        <i class=\"fa fa-pencil fa-lg\" title=\"Chinh Sua San Phan\" (click)=\"editProduct(product)\"></i>\r\n                        <i class=\"fa fa-trash-o fa-lg\" title=\"Xoa San Phan\" (click)=\"deleteProduct(product)\"></i>\r\n                    </div>\r\n                </div>\r\n                \r\n            </div>\r\n        </div>\r\n    </div>\r\n</div>\r\n\r\n<div *ngIf=\"isHideList\">\r\n    <div class=\"product-view-container\" *ngIf=\"isView\">\r\n        <product-view [product]=\"product\" (onBackProducts)=\"backProducts($event)\"></product-view>\r\n    </div>\r\n\r\n    <div class=\"product-modify-container\" *ngIf=\"!isView\">\r\n        <product-modify [product]=\"product\" (onSaveProduct)=\"saveProduct($event)\" \r\n            (onCancelProduct)=\"cancelProduct($event)\"></product-modify>\r\n    </div>\r\n</div>"

