module.exports = "<div class=\"title-for-upload-thanh-toan\"> Chon file de upload</div>\r\n<input type=\"file\" (change)=\"fileChangeEvent($event)\" placeholder=\"Upload file...\" name=\"userPhoto\" multiple/>\r\n\r\n<div class=\"html5-progress-bar\" *ngIf=\"numberOfDataUpload > 0\" >\r\n    <div class=\"progress-bar-wrapper\">\r\n        <progress id=\"progressbar\" value=\"{{numberOfDataUpload}}\" max=\"100\"></progress>\r\n        <span class=\"progress-value\">{{numberOfDataUpload}}%</span>\r\n    </div>\r\n</div>\r\n\r\n<div class=\"file-upload-panel\">\r\n    <div>Danh sach file trong thanh toan</div>\r\n    <div class=\"file-thanh-toan-item\" *ngFor=\"let file of fileSystems\"> \r\n        <div class=\"content-file\">\r\n            <img class=\"image-file\" src=\"{{file.pathFile}}\" width=\"100\">\r\n            <div class=\"file-name\"> {{file.originalName}}</div>\r\n            <div class=\"path-file\"> {{file.pathFile}}</div>\r\n        </div>\r\n\r\n        <div class=\"delete-file\">\r\n            <a href=\"javascript:void(0)\" (click)=\"deleteFile(file)\">\r\n                <i class=\"fa fa-trash-o fa-fw\"> </i>\r\n            </a>\r\n        </div>\r\n    </div>\r\n</div>\r\n\r\n<div class=\"content-panel\">\r\n    <p-editor [(ngModel)]=\"thanhtoan.content\" [style]=\"{'height':'320px'}\"></p-editor>\r\n    <button class=\"btn-cancel\" (click)=\"cancelThanhToan()\">Huy</button>\r\n    <button class=\"btn-save\" (click)=\"saveThanhToan()\">Luu</button>\r\n</div>\r\n\r\n"

